const {ccclass, property} = cc._decorator;

let _auto: boolean = true;

const CocosVersion = cc.Enum({
    VERSION_1: 0,
    VERSION_2: 1,
    VERSION_3: 2,
});

@ccclass
export default class PropertyStudy extends cc.Component {
    @property({type: cc.Label, displayName: "标题", tooltip: "某某界面的标题"})
    title: cc.Label = null;

    @property({type: cc.Integer, slide: true, min: 0, max: 100, visible:()=>{
        if (_auto) {
            return true;
        } else {
            return false;
        }
    }})
    int: number = 1;

    // 目前编辑器中 slide 滑动条数值修改精度固定是 0.1，不受 slide 参数以及编辑器设置影响
    @property({type: cc.Float, slide: true, min: 0, max: 100})
    float: number = 1;

    // 对于普通的浮点值修改，数值精度会受到 step 或编辑器设置的影响.
    // 如果需要使修改生效，则要先修改原属性名后回到编辑器，再回到脚本中改为原来的属性名即可
    @property({type: cc.Float, step: 0.1, min: 0, max: 100})
    float2: number = 1;

    @property({readonly: true})
    string: string = "Cocos";
    
    // 目前 readOnly 参数对多行文本输入框无效
    @property({multiline: true})
    string2: string = "";

    @property({type: cc.Boolean})
    get auto () {
        return _auto;
    }

    set auto (value: boolean) {
        _auto = value;
    }

    @property({type: CocosVersion})
    version = CocosVersion.VERSION_1;
}
